body {
  margin: 0;
  padding: 0;
}

.board {
  height: 100vh;
  min-height: 800px;
  position: relative;
  overflow: hidden;
  background: rgb(0, 0, 0) url(../images/board-bg.jpg) center bottom;
  background-size: cover;
}

